import styled from 'styled-components'

export const Wrapper = styled.div`
  padding: 28px;
  display: flex;
  justify-content: flex-end;
  padding-right: 0px;
`

export const MessageBubble = styled.div`
  background-color: #fff;
  filter: drop-shadow(0px 4px 16px rgba(52, 51, 91, 0.35));
  width: 300px;
  height: 65px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
`

export const MessageText = styled.div`
  line-height: 19px;
`

export const BubbleTriangle = styled.div`
  background-color: white;
  width: 14px;
  height: 14px;
  position: absolute;
  right: -5px;
  top: 32px;
  transform: rotate(45deg);
`

export const ChatCircle = styled.div`
  background-color: #263A96;
  width: 64px;
  height: 64px;
  margin-left: 15px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`

export const ChatIcon = styled.div`
  padding-top: 4px;
`
