import styled from 'styled-components'
import { ReactComponent as ChatCloseButton } from '../../assets/ChatCloseButton.svg'
import { ReactComponent as SendAMessage } from '../../assets/SendAMessage.svg'
import { ReactComponent as ChatEmoji } from '../../assets/ChatEmojiIcon.svg'
import { ReactComponent as Logo } from '../../assets/Logo.svg'

export const Wrapper = styled.div`
  width: 380px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 8px 32px rgb(52 51 91 / 35%);
  margin-left: auto;
  background-color: white;
`

export const HeaderWrapper = styled.div`
  background-color: #263A96;
  color: white;
  padding: 25px 20px 38px 25px;
`

export const Header = styled.div`
  display: flex;
`

export const AvatarWrapper = styled.div`
  background-color: #BAA9F6;
  width: 57px;
  height: 57px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
`

export const InfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`

export const HeaderTitle = styled.div`
  font-family: 'Mulish';
  font-size: 18px;
`

export const SubtitleWrapper = styled.div`
  display: flex;
  align-items: center;
`

export const OnlineStatus = styled.div`
  width: 6px;
  height: 6px;
  background-color: #56B8A7;
  border-radius: 50%;
  margin-right: 5px;
  margin-top: 2px;
`

export const HeaderSubtitle = styled.div`
  font-size: 14px;
  opacity: 0.6;
`

export const ChatCloseButtonIcon = styled(ChatCloseButton)`
  cursor: pointer;
`

export const CloseButtonWrapper = styled.div`
  margin-left: auto;
`

export const BodyWrapper = styled.div`
  padding: 37px 20px 23px 25px;
  /* height: 388px; */
`

export const OptionsBody = styled.div`
  margin-bottom: 37px;
`

export const BubbleLeft = styled.div`
  background-color: rgb(180 192 200 / 30%);
  padding: 16px 28px;
  border-radius: 40px 40px 40px 0px;
  font-family: 'Mulish';
  text-align: left;
  font-weight: 600;
  margin-bottom: 23px;
`

export const BodyRight = styled.div`
`

export const BubbleText = styled.div`
`

export const Option = styled.div`
  color: #56B8A7;
  text-align: left;
  border: 1px solid #56B8A7;
  border-radius: 10px;
  padding: 8px 23px;
  margin-bottom: 15px;
  font-weight: 700;
  font-family: 'Mulish';
  font-size: 14px;
  cursor: pointer;
  &:hover {
    background-color: #56B8A7;
    color: white;
  }
`

export const OptionText = styled.div`
`

export const ResponseWrapper = styled.div`
  background-color: #EEF1F4;
  height: 61px;
  border-radius: 37px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 17px;
`

export const ChatEmojiIcon = styled(ChatEmoji)`
  position: absolute;
  left: 23px;
  cursor: pointer;
`

export const ResponseInput = styled.input`
  border: none;
  background-color: #EEF1F4;
  height: 100%;
  padding: 0 10px;
  font-size: 16px;
  font-family: 'Mulish';
  color: #052C48;
  font-weight: 600;
  line-height: 20px;
  color: black;

  ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: black;
    opacity: 1; /* Firefox */
  }

  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: black;
  }

  ::-ms-input-placeholder { /* Microsoft Edge */
    color: black;
  }
`

export const SendMessageIconWrapper = styled.div`
  background-color: #263A96;
  height: 61px;
  width: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 37px;
  margin-left: auto;
  position: absolute;
  top: 0;
  right: 0;
`

export const SendMessageIcon = styled(SendAMessage)`
  cursor: pointer;
  margin-top: -3px;
`

export const LogoIcon = styled(Logo)`
`

export const FooterWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
`

export const FooterText = styled.div`
  color: #B4C0C8;
  margin-right: 5px;
  font-weight: 700;
  font-family: "Mulish";
  font-size: 12px;
  letter-spacing: 0.08px;
`