import { useState } from 'react'
import { ChatWindow } from '../ChatWindow/ChatWindow'
import { ChatPluginPrompt } from '../ChatPluginPrompt/ChatPluginPrompt'
import * as S from './LiveChat.styles'

export const LiveChat = () => {

  const [isOpen, setIsOpen] = useState(false)

  if (!isOpen) {
    return <ChatPluginPrompt isActive={isOpen} handleClick={() => setIsOpen(!isOpen)} />
  }

  return (
    <S.ChatWindowWrapper>
      <ChatWindow
        handleClose={() => setIsOpen(false)}
      />
      <ChatPluginPrompt
        isActive={isOpen}
        handleClick={() => setIsOpen(!isOpen)}
      />
    </S.ChatWindowWrapper>
  )
}
