import * as S from './ChatWindow.styles'
import { ReactComponent as ChatAvatarPlaceholder } from '../../assets/ChatAvatarPlaceholder.svg'

interface ChatWindowProps {
  handleClose: () => void
}

export const ChatWindow = ({ handleClose }: ChatWindowProps) => {
  const mockData = [
    {
      text: "Cześć, Jestem Twoim asystentem, w czym mogę pomóc?",
      options: [
        {
          text: "Chcę dowiedzieć się więcej o ofercie",
        },
        {
          text: "Kontakt z personelem",
        }
      ]
    },
  ]

  return (
    <S.Wrapper>
      <S.HeaderWrapper>
        <S.Header>
          <S.AvatarWrapper>
            <ChatAvatarPlaceholder />
          </S.AvatarWrapper>
          <S.InfoWrapper>
            <S.HeaderTitle>Twój osobisty asystent</S.HeaderTitle>
            <S.SubtitleWrapper>
              <S.OnlineStatus />
              <S.HeaderSubtitle>Odpisuję natychmiast</S.HeaderSubtitle>
            </S.SubtitleWrapper>
          </S.InfoWrapper>
          <S.CloseButtonWrapper>
            <S.ChatCloseButtonIcon onClick={() => handleClose()}/>
          </S.CloseButtonWrapper>
        </S.Header>
      </S.HeaderWrapper>
      <S.BodyWrapper>
        {mockData.map((data) => (
          <>
            <S.BubbleLeft>
              {data.text}
            </S.BubbleLeft>
            <S.OptionsBody>
              {
                data.options.map((option) => (
                  <S.Option>
                    <S.OptionText>
                      {option.text}
                    </S.OptionText>
                  </S.Option>)
                )
              }
            </S.OptionsBody>
          </>
        )
        )}
        <S.ResponseWrapper>
          <S.ChatEmojiIcon />
          <S.ResponseInput
            placeholder="Napisz wiadomość"
          />
          <S.SendMessageIconWrapper>
            <S.SendMessageIcon />
          </S.SendMessageIconWrapper>
        </S.ResponseWrapper>
        <S.FooterWrapper>
          <S.FooterText>
            Wysłane dzięki
          </S.FooterText>
          <S.LogoIcon />
        </S.FooterWrapper>
      </S.BodyWrapper>
    </S.Wrapper>
  )
}
