import * as S from './ChatPluginPrompt.styles'
import { ReactComponent as ChatIcon } from '../../assets/ChatIcon.svg'
import { ReactComponent as ChatCloseButton } from '../../assets/ChatCloseButton.svg'

interface ChatPluginPromptProps {
  isActive: boolean;
  handleClick: () => void;
}

export const ChatPluginPrompt = ({ handleClick, isActive }: ChatPluginPromptProps) => {
  if (isActive) {
    return (
      <S.Wrapper>
        <S.ChatCircle onClick={() => handleClick()}>
          <S.ChatIcon>
            <ChatCloseButton />
          </S.ChatIcon>
        </S.ChatCircle>
      </S.Wrapper>
    )
  }
  return (
    <S.Wrapper>
      <S.MessageBubble>
        <S.MessageText>
          Cześć 👋 w czym mogę pomóc?
        </S.MessageText>
        <S.BubbleTriangle />
      </S.MessageBubble>
      <S.ChatCircle onClick={() => handleClick()}>
        <S.ChatIcon>
          <ChatIcon />
        </S.ChatIcon>
      </S.ChatCircle>
    </S.Wrapper>
  )
}
